<template>
    <div class="c-author-gift">
        <el-button
            class="u-btn"
            icon="Present"
            @click="openGiftDialog"
            :disabled="isSelf || !status"
            :title="btnTitle"
            plain
            ></el-button
        >

        <el-dialog
            title=""
            v-model="visible"
            :append-to-body="true"
            class="c-author-gift-dialog"
            v-if="status"
        >
            <div class="u-content">
                <div class="u-left">
                    <em class="u-label"> </em>
                    <b>{{ left }}</b>
                    <a class="u-charge" :href="chargeLink" target="_blank">[]</a>
                </div>
                <div class="u-list">
                    <em class="u-label"> </em>
                    <div class="u-points">
                        <el-radio-group v-model="count">
                            <el-radio :label="item" v-for="item in fitPoints" :key="item" border>
                                <b>{{ item }}</b
                                >
                            </el-radio>
                        </el-radio-group>
                    </div>
                </div>
                <div class="u-msg">
                    <em class="u-label"> </em>
                    <div class="u-input">
                        <el-input
                            v-model="remark"
                            placeholder=""
                            :minlength="2"
                            :maxlength="30"
                            show-word-limit
                        ></el-input>
                    </div>
                </div>
            </div>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="visible = false"> </el-button>
                    <el-button type="primary" @click="submit" :disabled="!ready || loading" :loading="loading"
                        > </el-button
                    >
                </span>
            </template>
        </el-dialog>
    </div>
</template>

<script>
import User from "@jx3box/jx3box-common/js/user";
import { sendCny, checkCnyStatus, checkGiftStatus } from "../../service/thx";
export default {
    name: "AuthorGift",
    props: ["uid"],
    components: {},
    data: function () {
        return {
            loading: false,
            visible: false,
            fitPoints: [50, 100, 250, 520, 666, 888, 1314, 2288, 3344, 6666, 8888, 28888, 36888, 68888],

            left: 0, //
            status: true, //
            chargeLink: "/vip/cny?redirect=" + location.href,

            count: 0, //
            remark: "",
        };
    },
    computed: {
        user() {
            return User.getInfo();
        },
        isSelf() {
            return this.uid == this.user.uid;
        },
        ready: function () {
            // 
            // 
            // 
            // 
            return !this.isSelf && this.left && this.left >= this.count && this.count && this.remark;
        },
        btnTitle: function () {
            if (this.isSelf) {
                return "";
            } else if (!this.status) {
                return "";
            }
            return "";
        },
    },
    watch: {
        uid: {
            immediate: true,
            handler: function (val) {
                val && this.load();
            },
        },
    },
    methods: {
        openGiftDialog: function () {
            if (!User.isLogin()) {
                User.toLogin();
                return;
            } else {
                this.visible = true;
            }
        },
        submit: async function () {
            this.loading = true;
            sendCny(this.user.uid, this.uid, this.count, this.remark).then((res) => {
                console.info("[AUTHOR.CNY]");
                // ID
                const id = res.data.data.id;

                console.info("[AUTHOR.CNY]");
                // 
                const loading = this.$loading({
                    lock: true,
                    text: "",
                    spinner: "el-icon-loading",
                    background: "rgba(255, 255, 255, 0.8)",
                });

                let n = 0;
                const timer = setInterval(async () => {
                    console.info(`[AUTHOR.CNY]${n + 1}`);
                    await checkCnyStatus(id)
                        .then((res) => {
                            // 
                            let status = res.data.data.finish;

                            // 
                            if (status) {
                                console.info(`[AUTHOR.CNY]`);
                                //
                                loading.close();
                                clearInterval(timer);
                                //  & 
                                this.visible = false;
                                this.loading = false;
                                this.$message({
                                    message: "",
                                    type: "success",
                                });

                                // 
                                this.left = this.left - this.count;
                                this.count = 100;
                                this.remark = "";
                            } else {
                                console.info(`[AUTHOR.CNY]`);
                            }
                        })
                        .finally(() => {
                            n += 1;
                            console.info(`[AUTHOR.CNY]${n}`);
                        });

                    // 3
                    if (n > 5) {
                        console.info(`[AUTHOR.CNY]${n}`);
                        //
                        loading.close();
                        clearInterval(timer);
                        //  & 
                        this.visible = false;
                        this.loading = false;
                        this.$message("");
                    }
                }, 1000);
            });
        },
        load: function () {
            // 
            User.isLogin() &&
                checkGiftStatus(this.uid).then((res) => {
                    this.left = res.data.data.cny || 0; // 
                    this.status = res.data.data.targetUserCanReceiveCNY; // 
                });
        },
    },
    created: function () {},
    mounted: function () {},
};
</script>

